import { createWebHistory, createRouter } from "vue-router";
import mn from './components/cnt01.vue';
import cnt01 from './components/cnt02.vue'

const routes = [
  {
    path: "/",
    component: mn,
  },
  {
    path:'/cnt01',
    component: cnt01,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 